import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTIONLINE_DETS_CREATE,
  API_PRODUCTIONLINE_DETS_UPDATE,
  API_PRODUCTIONLINE_DETS_DETAIL
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.PRODUCTIONLINE_DET_UPDATE,
  data: object
})

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.PRODUCTIONLINE_DET_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_PRODUCTIONLINE_DETS_UPDATE
      : API_PRODUCTIONLINE_DETS_CREATE
  getData({ api, ...reqData })
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.PRODUCTIONLINE_DET_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch((err) => {
      dispatch({
        type: actionType.PRODUCTIONLINE_DET_FAIL
      })
    })
}
// 获取 - 单个产线数据
export const getSingleDet = (reqData, callBack) => (dispatch) => {
  getData({ api: API_PRODUCTIONLINE_DETS_DETAIL, ...reqData })
    .then((data) => {
      callBack && callBack(data)
      const { result } = data
      const {
        lineList = [],
        lineCode = '',
        lineName = '',
        locationCode = '',
        locationName = '',
        costCode = '',
        costName = '',
        workshopCode = '',
        workshopName = '',
        warehouseCode = '',
        warehouseName = '',
        productWarehouseCode = '',
        productWarehouseName = '',
        finishedProductCode = '',
        finishedProductName = ''
      } = result
      if (lineList && lineList.length > 0) {
        lineList.map((item, index, arr) => {
          const { lineType = '', lineTypeName } = item
          item.lineType = { value: `${lineType}`, children: `${lineTypeName}` }
        })
      }
      let dataFormater = {
        ...result,
        line: {
          value: `${lineCode} ${lineName}`,
          children: `${lineCode} ${lineName}`
        },
        warehouse: {
          value: `${warehouseCode} ${warehouseName}`,
          children: `${warehouseCode} ${warehouseName}`
        },
        productWarehouse: {
          value: `${productWarehouseCode} ${productWarehouseName}`,
          children: `${productWarehouseCode} ${productWarehouseName}`
        },
        finishedWarehouse: {
          value: `${finishedProductCode} ${finishedProductName}`,
          children: `${finishedProductCode} ${finishedProductName}`
        },
        workshop: {
          value: `${workshopCode} ${workshopName}`,
          children: `${workshopName}`
        },
        location: {
          value: `${locationCode} ${locationName}`,
          children: `${locationCode} ${locationName}`
        },
        cost: {
          value: `${costCode} ${costName}`,
          children: `${costCode} ${costName}`
        }
      }
      dispatch({
        type: actionType.PRODUCTIONLINE_DET_UPDATE,
        data: {
          modalData: dataFormater,
          tableDataSource: (lineList && lineList.length > 0 && lineList) || []
        }
      })
    })
    .catch((err) => {
      console.error(err)
      dispatch({
        type: actionType.PRODUCTIONLINE_DET_FAIL
      })
    })
}



