import React, { PureComponent } from 'react'
import { message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { delINArray } from 'util'
@connect(
  state => ({ ...state.productionLineDet }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { update, tableDataSource = [] } = this.props
    const newData = {
      id: Date.now(),
      workTime: '8',
      endTime: '17',
      lineType: '',
      restNumber: '1',
      startTime: '08'
    }
    let tableData =
      tableDataSource &&
      Array.isArray(tableDataSource) &&
      tableDataSource.length > 0
        ? tableDataSource.concat(newData)
        : [newData]
    update({ tableDataSource: tableData })
  }
  // 删除
  handleDel = () => {
    const { update, selectedRowKeys, tableDataSource = [], form } = this.props
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      // 将form表单中的数据清除掉
      const list = form.getFieldValue('productLineAbilityList')
      let formArr = delINArray(selectedRowKeys, list)
      formArr &&
        formArr.length > 0 &&
        formArr.map((item, index, arr) => {
          const {
            lineType,
            startTime,
            endTime,
            restNumber,
            workTime,
            id = ''
          } = item
          let obj = {
            [`productLineAbilityList[${index}][lineType]`]: lineType,
            [`productLineAbilityList[${index}][startTime]`]: startTime,
            [`productLineAbilityList[${index}][endTime]`]: endTime,
            [`productLineAbilityList[${index}][restNumber]`]: restNumber,
            [`productLineAbilityList[${index}][workTime]`]: workTime,
            [`productLineAbilityList[${index}][id]`]: id
          }
          this.props.form.setFieldsValue(obj)
        })
      // 将form表单中的数据清除掉
      update({
        tableDataSource: delINArray(selectedRowKeys, tableDataSource),
        selectedRowKeys: []
      })
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    return (
      <div className={style.table_tool_det_proLine}>
        <span onClick={this.handleAdd} className={style.table_tool_det_box}>
          <Button type='primary'>新增</Button>
        </span>
        <span onClick={this.handleDel}>
          <Button type='primary' style={{ cursor: 'pointer' }}>
            删除
          </Button>
        </span>
      </div>
    )
  }
}



